{"id":10,"name_pol":"Propranolol","name_lat":"Propranololi","dosage":40,"img_link":"https://upload.wikimedia.org/wikipedia/commons/a/a5/Propranolol.svg","description":"Nieselektywny lek β-adrenolityczny (β-bloker), bez wewnętrznej aktywności sympatykomimetycznej, mający działanie stabilizujące błonę komórkową. Blokuje działanie amin katecholowych (a konkretnie adrenaliny i noradrenaliny) na serce, powodując spadek zapotrzebowania serca na tlen oraz zwolnienie częstości jego pracy i w konsekwencji spadek ciśnienia krwi (zwłaszcza skurczowego). Ze względu na blokowanie również receptorów B-2 propranolol powoduje obkurczenie naczyń, a tym samym szybszy zanik naczyniaków","application":"Nadciśnienie tętnicze, Tachykardia, Zaburzenia rytmu serca, Hipertyreoza, Drżenie samoistne, Akatyzja","sub_group":"β-bloker","lethal_dose":565},
{"id":11,"name_pol":"Karbamazepina","name_lat":"Carbamazepinum","dosage":200,"img_link":"https://upload.wikimedia.org/wikipedia/commons/3/35/Carbamazepine.svg","description":"Pochodna dibenzoazepiny, stosowana jako lek psychotropowy, przeciwdrgawkowy i stabilizujący nastrój, używany głównie do leczenia padaczki i choroby afektywnej dwubiegunowej. Znajduje również zastosowanie w leczeniu nerwobóli oraz w leczeniu schizofrenii i innych psychoz jako lek potencjalizujący działanie leków przeciwpsychotycznych.","application":"Padaczka,Zaburzenia afektywne dwubiegunowe,Nerwobóle","sub_group":"Dibenzoazepiny","lethal_dose":114},
{"id":12,"name_pol":"Kwetiapina","name_lat":"Quetiapinum","dosage":25,"img_link":"https://upload.wikimedia.org/wikipedia/commons/1/1f/Quetiapine.svg","description":"Lek z grupy dibenzotiazepin, lek przeciwpsychotyczny atypowy (drugiej generacji) stosowany w leczeniu różnych zaburzeń psychicznych, zwłaszcza schizofrenii, zaburzeń afektywnych dwubiegunowych i ciężkich epizodów depresji. Nierzadko kwetiapina stosowana jest także w leczeniu bezsenności, zaburzeń zachowania związanych z chorobami otępiennymi, zaburzeń lękowych, a także zespołu Tourette’a i zaburzeń obsesyjno-kompulsyjnych (OCD, nerwica natręctw).","application":"Schizofrenia, Zaburzenia afektywne dwubiegunowe, Depresja, Choroby otępienne","sub_group":"Dibenzotiazepiny","lethal_dose":70},
{"id":13,"name_pol":"Benzylopenicylina","name_lat":"Benzylpenicillium","dosage":12000,"img_link":"https://upload.wikimedia.org/wikipedia/commons/7/7c/Penicillin-G.png","description":"Lek z grupy penicylin, antybiotyk β-laktamowy wytwarzany przez pędzlaki Penicillium chrysogenum (syn. Penicillium notatum) wrażliwy na β-laktamazy. Ze względu na strukturalne podobieństwo do alaniny, która jest składnikiem mureiny, benzylopenicylina blokuje aktywność transpeptydaz biorących udział w tworzeniu wiązań pomiędzy pentapeptydami mukopolisacharydu ściany komórkowej bakterii. W dalszym etapie, na skutek aktywacji hydrolaz komórkowych, zachodzi liza komórki bakteryjnej.","application":"Zakażenia bakteryjne, Stany zapalne","sub_group":"Penicyliny","lethal_dose":24},
{"id":14,"name_pol":"Chloropromazyna","name_lat":"Chlorpromazinum","dosage":25,"img_link":"https://upload.wikimedia.org/wikipedia/commons/4/47/Chlorpromazine.svg","description":"Lek psychotropowy o działaniu przeciwpsychotycznym, przeciwautystycznym i uspokajającym. Lek zaliczany do leków typowych działających głównie na receptory dopaminergiczne, pochodna fenotiazyny o budowie zbliżonej do chlorprotiksenu. Jest pierwszym skutecznym neuroleptykiem wprowadzonym do lecznictwa.","application":"Schizofrenia,Stany maniakalne,Psychozy","sub_group":"Psychotropy","lethal_dose":14}
